function createImgNode(url) {
  const clipImg = document.createElement('img');
  clipImg.className = 'clipImg';
  clipImg.setAttribute('src', url);
  clipImg.setAttribute('alt', 'img');

  return clipImg;
}

function linkCreateTitleNode(item) {
  const linkTitle = document.createElement('a');
  linkTitle.setAttribute('href', `https://www.youtube.com/watch?v=${item}`);
  return linkTitle;
}

function createTitleNode(item) {
  const title = document.createElement('h4');
  title.innerHTML = item;
  return title;
}

function createAuthorContainerNode() {
  const authorContainer = document.createElement('div');
  authorContainer.className = 'authorContainer';
  return authorContainer;
}

function createAuthorIconNode() {
  const authorIcon = document.createElement('img');
  authorIcon.className = 'authorImg';
  authorIcon.setAttribute('src', 'src/icon/author.png');
  authorIcon.setAttribute('alt', 'img');
  return authorIcon;
}

function createAuthorTextNode(item) {
  const authorText = document.createElement('p');
  authorText.innerHTML = item;
  authorText.className = 'authorText';
  return authorText;
}

function createUploadDateContainerNode() {
  const uploadDateContainer = document.createElement('div');
  uploadDateContainer.className = 'uploadDateContainer';
  return uploadDateContainer;
}

function createUploadDateIconNode() {
  const uploadDateIcon = document.createElement('img');
  uploadDateIcon.className = 'uploadDateIcon';
  uploadDateIcon.setAttribute('src', 'src/icon/calendar.png');
  uploadDateIcon.setAttribute('alt', 'img');
  return uploadDateIcon;
}

function createUploadDateTextNode(item) {
  const uploadDateText = document.createElement('p');
  const date = new Date(item);
  uploadDateText.innerHTML = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
  uploadDateText.className = 'uploadDateText';
  return uploadDateText;
}

function createViewCountContainerNode() {
  const viewCountContainer = document.createElement('div');
  viewCountContainer.className = 'viewCountContainer';
  return viewCountContainer;
}

function createViewCountIconNode() {
  const viewCountIcon = document.createElement('img');
  viewCountIcon.className = 'viewCountIcon';
  viewCountIcon.setAttribute('src', 'src/icon/views.png');
  viewCountIcon.setAttribute('alt', 'img');
  return viewCountIcon;
}

function createViewCountTextNode() {
  const viewCountText = document.createElement('p');
  viewCountText.innerHTML = 'many views';
  viewCountText.className = 'viewCountText';
  return viewCountText;
}

function createDescriptionNode(item) {
  const description = document.createElement('p');
  description.innerHTML = item;
  description.className = 'description';
  return description;
}


export default function createCard(item) {
  const { snippet } = item;

  const li = document.createElement('li');

  const img = createImgNode(snippet.thumbnails.high.url);
  li.appendChild(img);

  const linkTitle = linkCreateTitleNode(item.id.videoId);
  li.appendChild(linkTitle);

  const title = createTitleNode(snippet.title);
  linkTitle.appendChild(title);

  const authorContainer = createAuthorContainerNode();
  li.appendChild(authorContainer);

  const authorIcon = createAuthorIconNode(snippet.channelTitle);
  authorContainer.appendChild(authorIcon);

  const authorText = createAuthorTextNode(snippet.channelTitle);
  authorContainer.appendChild(authorText);

  const uploadDateContainer = createUploadDateContainerNode();
  li.appendChild(uploadDateContainer);

  const uploadDateIcon = createUploadDateIconNode();
  uploadDateContainer.appendChild(uploadDateIcon);

  const uploadDateText = createUploadDateTextNode(snippet.publishedAt);
  uploadDateContainer.appendChild(uploadDateText);

  const viewCountContainer = createViewCountContainerNode();
  li.appendChild(viewCountContainer);

  const viewCountIcon = createViewCountIconNode();
  viewCountContainer.appendChild(viewCountIcon);

  const viewCountText = createViewCountTextNode();
  viewCountContainer.appendChild(viewCountText);

  const description = createDescriptionNode(snippet.description);
  li.appendChild(description);

  return li;
}
