import createCard from '../Card/Ð¡ard';
import { renderSwitch } from './renderSwitch';
import emptyResponse from '../Error/emptyResponse';

export default function ListRenderer() {
  let list;

  return (items = []) => {
    const wrapper = document.getElementById('wrapper');

    if (!list) {
      list = document.createElement('ul');
      list.className = 'slider';
      list.setAttribute('id', 'slider');
    } else {
      for (let i = list.children.length - 1; i >= 0; i -= 1) {
        list.removeChild(list.children[i]);
      }
    }

    items.map(createCard).forEach((li) => {
      list.appendChild(li);
    });
    wrapper.appendChild(list);
    wrapper.appendChild(emptyResponse);
    wrapper.appendChild(renderSwitch);
  };
}
