/* eslint-disable import/no-mutable-exports */
// eslint-disable-next-line prefer-const
let counterSwitch = 1;

export { counterSwitch };

export function counterSwitchPlus() {
  counterSwitch += 1;
}

export function counterSwitchMinus() {
  counterSwitch -= 1;
}

export function counterSubmit() {
  counterSwitch = 1;
}
