import Observable from './utils/Observable';
import AppView from './AppView';
import { checkSwitch, renderSwitch } from './AppView/renderSwitch';
import twoStepBack from './Switch/twoStepBackButton';
import oneStepBack from './Switch/oneStepBackButton';
import oneStepFront from './Switch/oneStepFrontButton';
import {
  counterSwitch, counterSwitchPlus, counterSwitchMinus, counterSubmit,
} from './Switch/counterSwitch';
import countButton from './Switch/countButton';
import createCard from './Card/Ð¡ard';


export default class App {
  constructor(initialState = { items: [], searchQuery: '', newUrl: '' }) {
    this.state = initialState;
  }

  start() {
    const key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
    const amountClips = 15;
    let pageToken = '';
    const baseUrl = `https://www.googleapis.com/youtube/v3/search?part=snippet&key=${key}&maxResults=${amountClips}&order=viewCount&pageToken=${pageToken}&q=${this.state.searchQuery}`;

    const searchQueryObserver = new Observable();
    let view;
    const self = this;
    const handleSubmit = async (event) => {
      event.preventDefault();
      try {
        counterSubmit();
        renderSwitch.style.display = 'grid';
        oneStepBack.style.display = 'none';
        twoStepBack.style.display = 'none';
        countButton.innerHTML = counterSwitch;
        const response = await fetch(`${baseUrl}${encodeURIComponent(this.state.searchQuery)}`);
        const data = await response.json();
        if (!data.items.length) {
          // eslint-disable-next-line no-use-before-define
          slider.style.display = 'none';
          // eslint-disable-next-line no-use-before-define
          emptyResponse.style.display = 'grid';
        }
        pageToken = `${data.nextPageToken}`;
        this.state.items = data.items;
        view.render(this.state.items);
      } catch (e) {
        console.log(e);
      }
    };

    searchQueryObserver.subscribe(async (query) => {
      this.state = { ...this.state, searchQuery: query };
    });

    view = new AppView({ searchQueryObserver, handleSubmit });
    view.render();

    const slider = document.getElementById('slider');
    const listSlider = slider.getElementsByTagName('li');
    const emptyResponse = document.getElementById('emptyResponse');


    function widthScreen() {
      const w = window;
      const d = document;
      const ee = d.documentElement;
      const g = d.getElementsByTagName('body')[0];
      const x = w.innerWidth || ee.clientWidth || g.clientWidth;
      return x;
    }


    function rulerSlider() {
      if ((listSlider.length - (counterSwitch * 4 - 1)) < 21) {
        (async () => {
          self.state.newUrl = `https://www.googleapis.com/youtube/v3/search?part=snippet&key=${key}&maxResults=${amountClips}&order=viewCount&pageToken=${pageToken}&q=${self.state.searchQuery}`;
          const response = await fetch(`${self.state.newUrl}${encodeURIComponent(self.state.searchQuery)}`);
          const data = await response.json();
          if (!data.items.length) {
            emptyResponse.style.display = 'grid';
          }
          pageToken = `${data.nextPageToken}`;
          self.state.items = data.items;
          self.state.items.map(createCard).forEach((li) => {
            slider.appendChild(li);
            if (widthScreen() > 1440) {
              for (let i = counterSwitch * 4; i < listSlider.length; i += 1) {
                listSlider[i].style.display = 'none';
              }
              if (widthScreen() <= 1440 && widthScreen() > 1024) {
                for (let i = counterSwitch * 3; i < listSlider.length; i += 1) {
                  listSlider[i].style.display = 'none';
                }
              }
              if (widthScreen() <= 1024) {
                for (let i = counterSwitch; i < listSlider.length; i += 1) {
                  listSlider[i].style.display = 'none';
                }
              }
            }
          });
        })();
      }
      if (widthScreen() > 1440) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }
        for (let i = (counterSwitch * 4) - 1; i > (counterSwitch * 4) - 5; i -= 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1440 && widthScreen() > 1024) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }
        for (let i = (counterSwitch * 3) - 1; i > (counterSwitch * 3) - 4; i -= 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1024) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }
        listSlider[counterSwitch - 1].style.display = 'block';
      }
    }

    twoStepBack.addEventListener('click', () => {
      counterSwitchMinus();
      counterSwitchMinus();
      checkSwitch();
      rulerSlider();
    });
    oneStepBack.addEventListener('click', () => {
      counterSwitchMinus();
      checkSwitch();
      rulerSlider();
    });
    oneStepFront.addEventListener('click', () => {
      counterSwitchPlus();
      checkSwitch();
      rulerSlider();
    });


    let isDown = false;
    let startX;
    let scrollLeft;
    let flagWalkPlus = false;
    let flagWalkMinus = false;


    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      startX = e.pageX - slider.offsetLeft;
      // eslint-disable-next-line prefer-destructuring
      scrollLeft = slider.scrollLeft;
      if (widthScreen() > 1440) {
        slider.style.width = '1800px';
        for (let i = (counterSwitch * 4) - 1; i < (counterSwitch * 4) + 4; i += 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1440 && widthScreen() > 1024) {
        slider.style.width = '1300px';
        for (let i = counterSwitch * 3; i < (counterSwitch * 3) + 3; i += 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1024) {
        slider.style.width = '850px';
      }
    });
    slider.addEventListener('mouseleave', () => {
      isDown = false;
    });
    slider.addEventListener('mouseup', () => {
      isDown = false;
      if (flagWalkPlus) {
        counterSwitchPlus();
        flagWalkPlus = false;
        rulerSlider();
      }
      if (flagWalkMinus) {
        counterSwitchMinus();
        flagWalkMinus = false;
      }
      if (widthScreen() > 1440) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }

        for (let i = (counterSwitch * 4) - 1; i > (counterSwitch * 4) - 5; i -= 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1440 && widthScreen() > 1024) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }

        for (let i = (counterSwitch * 3) - 1; i > (counterSwitch * 3) - 4; i -= 1) {
          listSlider[i].style.display = 'block';
        }
      }
      if (widthScreen() <= 1024) {
        for (let i = 0; i < listSlider.length; i += 1) {
          listSlider[i].style.display = 'none';
        }
        listSlider[counterSwitch - 1].style.display = 'block';
      }
      slider.style.width = '100%';
      countButton.innerHTML = counterSwitch;
      checkSwitch();
    });
    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      const walk = (x - startX) * 4; // scroll-fast
      slider.scrollLeft = scrollLeft - walk;
      if (walk < -750) {
        flagWalkPlus = true;
      }
      if (walk > 750 && counterSwitch > 1) {
        flagWalkMinus = true;
      }
      if (flagWalkPlus && (widthScreen() > 1440)) {
        for (let i = counterSwitch - 1; i < counterSwitch * 4; i += 1) {
          listSlider[i].style.display = 'none';
        }
      }
      if (flagWalkPlus && (widthScreen() <= 1440 && widthScreen() > 1024)) {
        for (let i = counterSwitch - 1; i < counterSwitch * 3; i += 1) {
          listSlider[i].style.display = 'none';
        }
      }
      if (!flagWalkPlus && !flagWalkMinus) {
        if (walk < -500) {
          flagWalkPlus = true;
        }
        if (walk > 500 && counterSwitch > 1) {
          flagWalkMinus = true;
        }
        if (flagWalkPlus && (widthScreen() <= 1024)) {
          listSlider[counterSwitch - 1].style.display = 'none';
          listSlider[counterSwitch].style.display = 'block';
        }
        if (flagWalkMinus && (widthScreen() <= 1024)) {
          listSlider[counterSwitch - 2].style.display = 'block';
          listSlider[counterSwitch - 1].style.display = 'none';
        }
      }
    });
  }
}


