import InputField from './InputField';
import ListRenderer from './ListRenderer';

export default class AppView {
  constructor({ searchQueryObserver, handleSubmit }) {
    this.searchQueryObserver = searchQueryObserver;
    this.handleSubmit = handleSubmit;
    this.renderList = ListRenderer();
  }

  render(items) {
    const wrapper = document.createElement('div');
    wrapper.setAttribute('class', 'wrapper');
    wrapper.setAttribute('id', 'wrapper');
    document.body.appendChild(wrapper);

    const input = InputField({ id: 'search', searchQueryObserver: this.searchQueryObserver, handleSubmit: this.handleSubmit });

    if (!items) {
      wrapper.appendChild(input);
      this.renderList();
    } else {
      this.renderList(items);
    }
  }
}
