// eslint-disable-next-line no-unused-vars
export default function InputField({ id, searchQueryObserver, handleSubmit = () => {} }) {
  const form = document.createElement('form');
  form.setAttribute('class', 'form');
  form.setAttribute('name', 'form');
  form.setAttribute('id', 'form');

  const buttonInput = document.createElement('button');
  buttonInput.setAttribute('type', 'submit');
  buttonInput.setAttribute('id', 'button');
  form.appendChild(buttonInput);

  const icon = document.createElement('i');
  icon.className = 'fas fa-search';
  buttonInput.appendChild(icon);

  const iconImg = document.createElement('img');
  iconImg.setAttribute('src', 'src/icon/search-solid.png');
  iconImg.setAttribute('class', 'form-button-iconImg');
  buttonInput.appendChild(iconImg);

  const textInput = document.createElement('input');
  textInput.setAttribute('type', 'text');
  textInput.setAttribute('value', '');
  textInput.setAttribute('id', 'input');
  form.appendChild(textInput);

  const input = form.querySelector('input');
  input.addEventListener('input', e => searchQueryObserver.notify(e.target.value));

  const button = form.querySelector('button');
  button.addEventListener('click', handleSubmit);
  const wrapper = document.getElementsByClassName('wrapper');
  if (wrapper.length === 2) document.body.removeChild(wrapper[1]);

  return form;
}
