import twoStepBack from '../Switch/twoStepBackButton';
import oneStepBack from '../Switch/oneStepBackButton';
import countButton from '../Switch/countButton';
import oneStepFront from '../Switch/oneStepFrontButton';
import { counterSwitch } from '../Switch/counterSwitch';

export function checkSwitch() {
  countButton.innerHTML = counterSwitch;

  if (counterSwitch === 1) {
    twoStepBack.style.display = 'none';
    oneStepBack.style.display = 'none';
  }

  if (counterSwitch === 2) {
    twoStepBack.style.display = 'none';
    oneStepBack.style.display = 'block';
  }

  if (counterSwitch > 2) {
    twoStepBack.style.display = 'block';
    oneStepBack.style.display = 'block';
  }
}

const renderSwitch = document.createElement('ul');
renderSwitch.className = 'switch';

renderSwitch.appendChild(twoStepBack);
renderSwitch.appendChild(oneStepBack);
renderSwitch.appendChild(countButton);
renderSwitch.appendChild(oneStepFront);

countButton.innerHTML = counterSwitch;

export { renderSwitch };
